import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import {useStaticQuery, graphql} from "gatsby"
import Img from "gatsby-image"
import foto1 from "../images/convert-audio-mac.png"
import PostIndex from "../components/postIndex/postIndex";
import PostIndex2 from  "../components/postIndex2/postIndex2";


const Mac = () => {
    return (
        <Layout sideNavigation={true}>
            <SEO
                title="Vitamina B2"
                meta="Toda la información que necesitas sobre la vitamina B2. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina B2?"
                description="Toda la información que necesitas sobre la vitamina B2. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina B2?"
                lang="es"
            />

            <h1>Beneficios y fuentes de vitamina B2</h1>
            <PostIndex2 linksData={[
                {"url": "#Papel", "text": "¿Qué hace la vitamina B2?"},
                {"url": "#Fuentes", "text": "Alimentos"},
                {"url": "#cantidad", "text": "Cantidad necesaria"},
                {"url": "#Deficiencia", "text": "Deficiencia"},
                {"url": "#Riesgos", "text": "Exceso"}
            ]}
            />
            <hr />
            <p class="introductionText">La vitamina B2, o riboflavina, es una de las ocho <a className="link" href="/vitaminaB">vitaminas B</a> que son esenciales para la salud humana. Se
                puede encontrar en las plantas y productos l&aacute;cteos. Es crucial para descomponer los
                componentes de los alimentos, absorber otros nutrientes y mantener los tejidos.</p>
            <p></p>
            <hr />
            <p>La vitamina B2 es una vitamina soluble en agua, por lo que se disuelve en el agua. Todas las vitaminas
                son solubles en agua o en grasa. Las vitaminas solubles en agua son transportadas por el torrente
                sangu&iacute;neo, y lo que no se necesita sale del cuerpo en la orina.</p>
            <p></p>
            <p>La gente necesita consumir vitamina B2 todos los d&iacute;as, porque el cuerpo s&oacute;lo puede
                almacenar peque&ntilde;as cantidades, y los suministros disminuyen r&aacute;pidamente.</p>
            <p></p>
            <p>La riboflavina se encuentra de forma natural en algunos alimentos, a&ntilde;adida a otros, y se puede
                tomar como suplemento. La mayor parte se absorbe en el intestino delgado.</p>
            <p></p>
            <h2 name="Papel">¿Qué hace la vitamina B2?</h2>

            <p>La vitamina B2 ayuda a descomponer las prote&iacute;nas, las grasas y los carbohidratos. Juega un papel
                vital en el mantenimiento del suministro de energ&iacute;a del cuerpo.</p>
            <p></p>
            <p>La riboflavina ayuda a convertir los carbohidratos en trifosfato de adenosina (ATP). El cuerpo humano
                produce ATP a partir de los alimentos, y el ATP produce energ&iacute;a a medida que el cuerpo lo
                requiere. El compuesto ATP es vital para almacenar energ&iacute;a en los m&uacute;sculos.</p>
            <p></p>
            <p>Junto con la vitamina A, la vitamina B es esencial para:</p>
            <p></p>
            <ul>
                <li>Mantener las membranas mucosas del sistema digestivo</li>
                <li>Mantener un h&iacute;gado sano</li>
                <li>Convertir el tript&oacute;fano en niacina, un amino&aacute;cido</li>
                <li>Manteniendo los ojos, los nervios, los m&uacute;sculos y la piel saludables</li>
                <li>Absorbiendo y activando el hierro, el &aacute;cido f&oacute;lico y las vitaminas B1, B3 y B6</li>
                <li>La producci&oacute;n de hormonas por las gl&aacute;ndulas suprarrenales</li>
                <li>Prevenci&oacute;n del desarrollo de cataratas</li>
                <li>El desarrollo fetal, especialmente en las zonas donde la deficiencia de vitaminas es com&uacute;n
                </li>
            </ul>

            <p>Algunas investigaciones sugieren que la vitamina B2 puede ayudar a prevenir las cataratas y el dolor de
                cabeza por migra&ntilde;a, pero se necesitan m&aacute;s estudios para confirmarlo.</p>
            <p></p>
            <p>Otros estudios han encontrado que en los ni&ntilde;os con autismo, los suplementos de vitaminas B2, B6 y
                magnesio parecen reducir los niveles de &aacute;cidos org&aacute;nicos anormales en la orina.</p>
            <p></p>
            <p></p>
            <h2 name="Fuentes">Alimentos con vitamina B2</h2>
            <p></p>
            <p>Estos son los alimentos que contienen vitamina B2:</p>
            <p></p>
            <ul>
                <li>La carne, el pescado y los productos l&aacute;cteos proporcionan la vitamina B2.</li>
                <li>El pescado, la carne y las aves de corral, como el pavo, el pollo, la carne de vacuno, los
                    ri&ntilde;ones
                    y el h&iacute;gado
                </li>
                <li>Huevos</li>
                <li>Productos l&aacute;cteos</li>
                <li>Esp&aacute;rragos</li>
                <li>Alcachofas</li>
                <li>Aguacates</li>
                <li>Cayena</li>
                <li>Grosellas</li>
                <li>Cereales enriquecidos</li>
                <li>Kelp</li>
                <li>Frijoles de Lima, frijoles de la marina y guisantes.</li>
                <li>Melaza</li>
                <li>Setas</li>
                <li>Nueces</li>
                <li>Perejil</li>
                <li>Calabazas</li>
                <li>Rosa mosqueta</li>
                <li>Sage</li>
                <li>Las patatas dulces</li>
                <li>Vegetales cruc&iacute;feros, como el br&oacute;coli, las coles de Bruselas, las espinacas, las hojas
                    de
                    diente de le&oacute;n y los berros.
                </li>
                <li>Panes integrales, panes enriquecidos y salvado de trigo</li>
                <li>Extracto de levadura</li>
            </ul>

            <p><b>La vitamina B2 es soluble en agua, por lo que cocinar los alimentos puede hacer que se pierda. Alrededor
                del doble de B2 se pierde al hervirla que al vapor o al microondas.</b></p>
            <p></p>
            <h2 name="#cantidad">&iquest;Cu&aacute;nta B2 necesitamos?</h2>
            <p>De acuerdo con la Universidad Estatal de Oreg&oacute;n, la dosis diaria recomendada (RDA) de vitamina B2
                para los hombres de 19 a&ntilde;os o m&aacute;s es de 1,3 miligramos por d&iacute;a, y para las mujeres
                es de 1,1 miligramos por d&iacute;a. Durante el embarazo, las mujeres deben tomar 1,4 miligramos por
                d&iacute;a, y cuando amamantan, 1,6 miligramos por d&iacute;a.</p>
            <p></p>
            <h2 name="Deficiencia">Deficiencia de vitamina B2</h2>
            <p>La deficiencia de vitamina B2 es un riesgo significativo cuando la dieta es deficiente, porque el cuerpo
                humano excreta la vitamina continuamente, por lo que no se almacena. Una persona que tiene una
                deficiencia de B2 normalmente tambi&eacute;n carece de otras vitaminas.</p>
            <p></p>
            <p>Hay dos tipos de deficiencia de riboflavina:</p>
            <p></p>
            <ul>
                <li>La deficiencia primaria de riboflavina ocurre cuando la dieta de la persona es pobre en vitamina
                    B2
                </li>
                <li>La deficiencia de riboflavina secundaria ocurre por otra raz&oacute;n, tal vez porque los intestinos
                    no
                    pueden absorber la vitamina adecuadamente, o el cuerpo no puede usarla, o porque se excreta
                    demasiado
                    r&aacute;pido.
                </li>
            </ul>

            <p>La deficiencia de riboflavina tambi&eacute;n se conoce como ariboflavinosis.</p>
            <p></p>
            <p>Los signos y s&iacute;ntomas de la deficiencia incluyen:</p>
            <p></p>

            <ul>
                <li>La falta de vitamina B2 puede provocar &uacute;lceras bucales y otras dolencias.</li>
                <li>Quelitis angular, o grietas en las esquinas de la boca</li>
                <li>Labios agrietados</li>
                <li>Piel seca</li>
                <li>La inflamaci&oacute;n del revestimiento de la boca</li>
                <li>Inflamaci&oacute;n de la lengua</li>
                <li>Ulceras bucales</li>
                <li>Labios rojos</li>
                <li>Dolor de garganta</li>
                <li>Dermatitis escrotal</li>
                <li>L&iacute;quido en las membranas mucosas</li>
                <li>Anemia por deficiencia de hierro</li>
                <li>Los ojos pueden ser sensibles a la luz brillante, y pueden picar, estar acuosos o inyectados en
                    sangre.
                </li>
            </ul>
            <p>Las personas que beben cantidades excesivas de alcohol corren un mayor riesgo de sufrir una deficiencia
                de vitamina B.</p>
            <p></p>
            <p></p>
            <h2 name="Riesgos">Exceso de vitamina B2</h2>
            <p>Normalmente, la vitamina B2 se considera segura. Una sobredosis es poco probable, ya que el cuerpo puede
                absorber hasta alrededor de 27 miligramos de riboflavina, y expulsa cualquier cantidad adicional en la
                orina.</p>
            <p></p>
            <p>Sin embargo, es importante hablar con un m&eacute;dico antes de tomar cualquier suplemento, especialmente
                porque estos pueden interferir con otros medicamentos.</p>
            <p></p>
            <p>Los suplementos pueden interactuar con otros medicamentos, y los suplementos de B2 pueden afectar la
                eficacia de algunos medicamentos, como los anticolin&eacute;rgicos y la tetraciclina.</p>
            <p></p>
            <p>A veces un m&eacute;dico puede recomendar la suplementaci&oacute;n, por ejemplo, si un paciente
                est&aacute; usando un medicamento que puede interferir con la absorci&oacute;n de la riboflavina.</p>
            <p></p>
            <p>Entre los medicamentos que pueden interferir con los niveles de riboflavina en el cuerpo se incluyen:</p>
            <p></p>
            <ul>
                <li>Antidepresivos tric&iacute;clicos, como la imipramina o el Tofranil</li>
                <li>Algunos medicamentos antipsic&oacute;ticos, como la clorpromazina o la torazina</li>
                <li>El metotrexato, utilizado para el c&aacute;ncer y las enfermedades autoinmunes, como la artritis
                    reumatoide.</li>
                <li>La fenito&iacute;na, o Dilantin, se utiliza para controlar las convulsiones</li>
                <li>Probenecida, para la gota</li>
                <li>Diur&eacute;ticos tiaz&iacute;dicos o diur&eacute;ticos.</li>
            </ul>

            <p>La doxorrubicina, un medicamento utilizado en la terapia del c&aacute;ncer, puede reducir los niveles de
                riboflavina, y la riboflavina puede afectar la forma en que funciona la doxorrubicina.</p>
            <p></p>
            <p>El Centro M&eacute;dico de la Universidad de Maryland (UMM) se&ntilde;ala que cantidades muy altas de
                vitamina B2 pueden provocar picor, entumecimiento, ardor o punzadas, orina amarilla o anaranjada y
                sensibilidad a la luz. Para prevenir un desequilibrio de las vitaminas B, sugieren usar una vitamina del
                complejo B si se necesita un suplemento.</p>
            <p></p>
        </Layout>
    )
}

export default Mac
